// Tracking.js
import React from 'react';

function Tracking({ trackedStocks, onRemoveTrack }) {
  return (
    <div>
      <h3 className="text-xl font-semibold mb-4">Tracked Stocks</h3>
      {trackedStocks.length ? (
        trackedStocks.map((stock) => (
          <div key={stock.symbol} className="p-2 border rounded mb-2 flex justify-between">
            <p>{stock.name} ({stock.symbol})</p>
            <button onClick={() => onRemoveTrack(stock.symbol)} className="text-red-500">
              Remove
            </button>
          </div>
        ))
      ) : (
        <p>No stocks tracked.</p>
      )}
    </div>
  );
}

export default Tracking;
