// StockPreview.js
import React from 'react';
import { Button } from '@mui/material';

function StockPreview({ stock, isOwned, onBuy, onSell, onTrack }) {
  return (
    <div className="bg-white shadow-lg p-4 rounded-lg relative">
      {/* Blurred background */}
      <div className="absolute inset-0 bg-black opacity-30 blur-lg"></div>

      <div className="relative z-10">
        <h2 className="text-2xl">{stock.name} ({stock.symbol})</h2>
        <p>Price: ${stock.price}</p>
        <p>Change: {stock.isUp ? 'Up' : 'Down'}</p>
        {isOwned && <p>Shares: {stock.shares}</p>}

        <div className="mt-4 flex gap-2">
          <Button variant="contained" color="primary" onClick={onBuy}>
            Buy
          </Button>
          {isOwned ? (
            <Button variant="outlined" color="secondary" onClick={onSell}>
              Sell
            </Button>
          ) : (
            <Button variant="outlined" color="secondary" onClick={onTrack}>
              Track
            </Button>
          )}
        </div>
      </div>
    </div>
  );
}

export default StockPreview;
