// SearchBar.js
import React from 'react';

function SearchBar({ onSearch }) {
  return (
    <div className="p-4">
      <input
        type="text"
        placeholder="Search stocks..."
        className="border rounded w-full p-2"
        onChange={(e) => onSearch(e.target.value)}
      />
    </div>
  );
}

export default SearchBar;
