import React, { useState } from 'react';
import SearchBar from './components/search/SearchBar.tsx';
import StockPreview from './components/StockPreview/StockPreview.tsx';
import Sidebar from './components/sidebar/Sidebar.tsx';
import PortfolioSummary from './components/PortfolioSummary/PortfolioSummary.tsx';
import TransactionHistory from './components/TransactionHistory/TransactionHistory.tsx';
import axios from 'axios';

function App() {
  const [stock, setStock] = useState(null); // The stock selected from the search
  const [portfolio, setPortfolio] = useState([]); // Owned stocks
  const [tracking, setTracking] = useState([]); // Tracked stocks
  const [history, setHistory] = useState([]); // Buy/sell transaction history

  // Handle search input
  const handleSearch = async (symbol) => {
    try {
      const response = await axios.post('http://localhost:5000/stock_information', { symbol });
      setStock(response.data.stock_info);
    } catch (error) {
      console.error('Error fetching stock information:', error);
    }
  };

  // Handle buying a stock
  const handleBuy = (newStock) => {
    setPortfolio((prevPortfolio) => {
      const existingStock = prevPortfolio.find((s) => s.symbol === newStock.symbol);
      if (existingStock) {
        existingStock.shares += 1; // Increment the number of shares if already owned
      } else {
        prevPortfolio.push({ ...newStock, shares: 1 }); // Add new stock with 1 share
      }
      return [...prevPortfolio];
    });

    // Add to tracking automatically
    if (!tracking.find((s) => s.symbol === newStock.symbol)) {
      setTracking((prevTracking) => [...prevTracking, newStock]);
    }

    // Add transaction to history
    setHistory((prevHistory) => [...prevHistory, { action: 'buy', stock: newStock }]);
  };

  // Handle selling a stock
  const handleSell = (sellStock) => {
    setPortfolio((prevPortfolio) => {
      return prevPortfolio
        .map((s) => (s.symbol === sellStock.symbol ? { ...s, shares: s.shares - 1 } : s))
        .filter((s) => s.shares > 0); // Remove stock if no shares left
    });

    // Add transaction to history
    setHistory((prevHistory) => [...prevHistory, { action: 'sell', stock: sellStock }]);
  };

  // Handle tracking a stock
  const handleTrack = (trackStock) => {
    if (!tracking.find((s) => s.symbol === trackStock.symbol)) {
      setTracking((prevTracking) => [...prevTracking, trackStock]);
    }
  };

  // Handle removing from tracking
  const handleRemoveTrack = (symbol) => {
    setTracking((prevTracking) => prevTracking.filter((s) => s.symbol !== symbol));
  };

  return (
    <div className="flex h-screen">
      {/* Sidebar for Portfolio/Tracking (left 30% of the screen) */}
      <div className="w-1/3 bg-gray-100">
        <Sidebar
          portfolio={portfolio}
          tracking={tracking}
          onRemoveTrack={handleRemoveTrack}
          onStockSelect={(selectedStock) => setStock(selectedStock)}
        />
      </div>

      {/* Main content (right 70% of the screen) */}
      <div className="w-2/3 flex flex-col">
        {/* Search Bar */}
        <div className="p-4">
          <SearchBar onSearch={handleSearch} />
        </div>

        {/* Stock Preview */}
        {stock && (
          <div className="flex-grow p-4">
            <StockPreview
              stock={stock}
              isOwned={!!portfolio.find((s) => s.symbol === stock.symbol)}
              onBuy={() => handleBuy(stock)}
              onSell={() => handleSell(stock)}
              onTrack={() => handleTrack(stock)}
            />
          </div>
        )}

        {/* Portfolio Summary & Transaction History */}
        <div className="flex justify-between p-4">
          <div className="w-1/2">
            <PortfolioSummary portfolio={portfolio} />
          </div>
          <div className="w-1/2">
            <TransactionHistory history={history} />
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
