// Sidebar.js
import React, { useState } from 'react';
import Portfolio from './Portfolio.tsx';
import Tracking from './Tracking.tsx';
function Sidebar() {
  const [activeTab, setActiveTab] = useState('portfolio');

  return (
    <div className="bg-gray-100 w-1/3 p-4">
      <div className="flex justify-between mb-4">
        <button
          className={`p-2 ${activeTab === 'portfolio' ? 'bg-blue-500 text-white' : 'bg-white'}`}
          onClick={() => setActiveTab('portfolio')}
        >
          Portfolio
        </button>
        <button
          className={`p-2 ${activeTab === 'tracking' ? 'bg-blue-500 text-white' : 'bg-white'}`}
          onClick={() => setActiveTab('tracking')}
        >
          Tracking
        </button>
      </div>
      {activeTab === 'portfolio' ? <Portfolio stocks={undefined} /> : <Tracking trackedStocks={undefined} onRemoveTrack={undefined} />}
    </div>
  );
}

export default Sidebar;
