// Portfolio.js
import React from 'react';

function Portfolio({ stocks }) {
  return (
    <div>
      <h3 className="text-xl font-semibold mb-4">Your Portfolio</h3>
      {stocks.length ? (
        stocks.map((stock) => (
          <div key={stock.symbol} className="p-2 border rounded mb-2">
            <p>{stock.name} ({stock.symbol}) - {stock.shares} shares</p>
          </div>
        ))
      ) : (
        <p>No stocks owned.</p>
      )}
    </div>
  );
}

export default Portfolio;
